/* CUSTOM CHECKBOX */

.checkbox {
  position: absolute;
  opacity: 0;
}

.label {
  font-size: 16px;
  color: #696969;
  position: relative;
  cursor: pointer;
  padding-left: 30px;
  display: inline-block;
}

.label:before {
  content: "";
  background-color: #fff;
  border: 2px solid #bebebe;
  border-radius: 2px;
  display: inline-block;
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0px;
  top: 1px;
}

.label:after {
  content: "\2713";
  border-radius: 5px;
  font-weight: 600;
  display: inline-block;
  position: absolute;
  left: 4px;
  color: #696969;
  transform: scale(0);
  transition: transform 0.2s linear;
}

.checkbox:checked + label:after {
  transform: scale(1);
}

