  height: 15rem;
  display: block;
  margin: 0 auto;
}

/* Slideshow container */

.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Next & previous buttons */

.slideshow-container .prev,
.slideshow-container .next {
  cursor: pointer;
  position: absolute;
  top: 47%;
  width: 30px;
  padding: 3px 10px;
  height: 30px;
  border-radius: 50%;
  margin-top: -18px;
  color: white !important;
  font-weight: bold;
  font-size: 16px;
  transition: 0.6s ease;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: #0c8c0c;
}

/* Position the "next button" to the right */

.next {
  right: 0;
  border-radius: 50%;
}

/* The dots/bullets/indicators */

.dot {
  cursor: pointer;
  height: 12px;
  width: 12px;
  margin: 0 2px;
  background-color: #74fc74;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active-slider,
.dot:hover {
  background-color: #0c8c0c;
}

/* Fading animation */

.slide-in {
  -webkit-animation-name: slide-in;
  -webkit-animation-duration: 1.5s;
  animation-name: slide-in;
  animation-duration: 1.5s;
}

@-webkit-keyframes slide-in {
  from {
    opacity: 0.4;
  }

  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    opacity: 0.4;
  }

  to {
    opacity: 1;
  }
}

