.security-settings
  .security-settings-container
  .modal
  .form-field
  input[type="radio"] {
  position: absolute;
  left: -9999px;
}

.register-container .register-form input[type="radio"] + label,
.homepage-filter input[type="radio"] + label,
.payment-prices input[type="radio"] + label,
#create-post .price-field input[type="radio"] + .label,
.customer-fulfillment-details .form-container input[type="radio"] + label,
.security-settings
  .security-settings-container
  .modal
  .form-field
  input[type="radio"]
  + label {
  position: relative;
  cursor: pointer;
  padding: 1px 0 0 30px;
  margin-bottom: 10px;
  display: block;
}

.payment-prices input[type="radio"] + label {
  display: inline;
}

.register-container .register-form input[type="radio"] + label:before,
.homepage-filter input[type="radio"] + label:before,
.payment-prices input[type="radio"] + label:before,
#create-post .price-field input[type="radio"] + .label:before,
#create-post .price-field input[type="radio"] + .label:before,
.security-settings
  .security-settings-container
  .modal
  .form-field
  input[type="radio"]
  + label:before,
.customer-fulfillment-details
  .form-container
  input[type="radio"]
  + label:before {
  content: "";
  background: #fff;
  border: 2px solid #0c8c0c;
  height: 22px;
  width: 22px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.register-container .register-form input[type="radio"] + label:after,
.homepage-filter input[type="radio"] + label:after,
.payment-prices input[type="radio"] + label:after,
#create-post .price-field input[type="radio"] + .label:after,
.security-settings
  .security-settings-container
  .modal
  .form-field
  input[type="radio"]
  + label:after,
.customer-fulfillment-details
  .form-container
  input[type="radio"]
  + label:after {
  content: "";
  background: #0c8c0c;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 5px;
  opacity: 0;
  transform: scale(2);
  transition: transform 0.3s linear, opacity 0.3s linear;
}

.register-container .register-form input[type="radio"]:checked + label:after,
.homepage-filter input[type="radio"]:checked + label:after,
#create-post .price-field input[type="radio"]:checked + label:after,
.payment-prices input[type="radio"]:checked + label:after,
.security-settings
  .security-settings-container
  .modal
  .form-field
  input[type="radio"]:checked
  + label:after,
.customer-fulfillment-details
  .form-container
  input[type="radio"]:checked
  + label:after {
  opacity: 1;
  transform: scale(1);
}

