.switch label input[type="checkbox"] {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch [type="checkbox"]:checked,
.switch [type="checkbox"]:not(:checked) {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

#myChart {
  margin-bottom: 50px;
