.switch.chat-switch label input[type="checkbox"]:checked + .lever {
  background-color: #ff9494;
}

.switch.chat-switch label input[type="checkbox"]:checked + .lever::after {
  background-color: #bf3535;
}

.switch.phone-switch label input[type="checkbox"]:checked + .lever {
  background-color: #ffc46c;
}

.switch.phone-switch label input[type="checkbox"]:checked + .lever::after {
  background-color: #e4921b;
}

.switch label input[type="checkbox"]:checked + .lever::after {
  background-color: #0c8c0c;
  left: 1.5rem;
}

.switch label .lever::after {
  content: "";
  position: absolute;
  display: inline-block;
  background-color: #eee7e7;
  border-radius: 1.3125rem;
  left: -0.3125rem;
  top: -0.1875rem;
  box-shadow: 0 0.0625rem 0.1875rem 0.0625rem rgba(0, 0, 0, 0.4);
  width: 1.3125rem;
  height: 1.3125rem;
  transition: left 0.3s ease, background 0.3s ease, box-shadow 1s ease;
}

.switch label input[type="checkbox"]:checked + .lever {
  background-color: #aaffaa;
}

.switch label .lever {
  content: "";
  display: inline-block;
  position: relative;
  background-color: #dfdfdf;
  border-radius: 0.9375rem;
  margin-right: 0.625rem;
  vertical-align: middle;
  margin: 0 1rem;
  width: 2.5rem;
  height: 0.9375rem;
  transition: background 0.3s ease;
}

