.register-container .register-form input[type="radio"] + label:before,
.homepage-filter input[type="radio"] + label:before,
.payment-prices input[type="radio"] + label:before,
#create-post .price-field input[type="radio"] + .label:before,
#create-post .price-field input[type="radio"] + .label:before,
.security-settings
  .security-settings-container
  .modal
  .form-field
