}

.activetoggle .slider:before {
  position: absolute;
  content: "";
  height: 23px;
  width: 30px;
  top: 1px;
  left: 1px;
  right: 0px;
  bottom: 1px;
  background-color: white;
  transition: all 0.3s linear;
}

.activetoggle input:checked + .slider {
  background-color: #0c8c0c;
}

.activetoggle input:checked + .slider:before {
  transform: translateX(58px);
}

.activetoggle .slider:after {
  content: "OFF";
  color: white;
  display: block;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 75%;
  font-size: 14px;
}

.activetoggle input:checked + .slider:after {
  content: "ON";
  left: 20%;
}

