.starability-result {
  position: relative;
  width: 150px;
  font-size: 0.1em;
  color: transparent;
}
.starability-result,
.starability-result:after {
  height: 30px;
  background-image: url("data:image/png;base64,");
}
.starability-result:after {
  content: " ";
  position: absolute;
  left: 0;
  background-position: 0 -30px;
}
.starability-result[data-rating="5"]:after {
  width: 150px;
}
.starability-result[data-rating="4"]:after {
  width: 120px;
}
.starability-result[data-rating="3"]:after {
  width: 90px;
}
.starability-result[data-rating="2"]:after {
  width: 60px;
}
.starability-result[data-rating="1"]:after {
  width: 30px;
}
@media screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi) {
  .starability-result,
  .starability-result:after {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
}
@-webkit-keyframes a {
  0% {
    -webkit-transform: scale(1) rotate(0deg);
    transform: scale(1) rotate(0deg);
  }
  99% {
    -webkit-transform: scale(4) rotate(90deg);
    transform: scale(4) rotate(90deg);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1) rotate(0deg);
    transform: scale(1) rotate(0deg);
    opacity: 0;
  }
}
@keyframes a {
  0% {
    -webkit-transform: scale(1) rotate(0deg);
    transform: scale(1) rotate(0deg);
  }
  99% {
    -webkit-transform: scale(4) rotate(90deg);
    transform: scale(4) rotate(90deg);
    opacity: 0;
  }
  to {
    -webkit-transform: scale(1) rotate(0deg);
    transform: scale(1) rotate(0deg);
    opacity: 0;
  }
}
.starability-growRotate {
  display: block;
  position: relative;
  width: 150px;
  min-height: 60px;
  padding: 0;
  border: none;
  will-change: contents;
}
.starability-growRotate > input {
  position: absolute;
  margin-right: -100%;
  opacity: 0;
}
.starability-growRotate > input:checked ~ label,
.starability-growRotate > input:focus ~ label {
  background-position: 0 0;
}
.starability-growRotate > input:checked + label,
.starability-growRotate > input:focus + label {
  background-position: 0 -30px;
}
.starability-growRotate > input[disabled]:hover + label {
  cursor: default;
}
.starability-growRotate > input:not([disabled]):hover ~ label {
  background-position: 0 0;
}
.starability-growRotate > input:not([disabled]):hover + label {
  background-position: 0 -30px;
}
.starability-growRotate > input:not([disabled]):hover + label:before {
  opacity: 1;
}
.starability-growRotate > input:focus + label {
  outline: 1px dotted #999;
}
.starability-growRotate .starability-focus-ring {
  position: absolute;
  left: 0;
  width: 100%;
  height: 30px;
  outline: 2px dotted #999;
  pointer-events: none;
  opacity: 0;
}
.starability-growRotate > .input-no-rate:focus ~ .starability-focus-ring {
  opacity: 1;
}
.starability-growRotate > label {
  position: relative;
  display: inline-block;
  float: left;
  width: 30px;
  font-size: 0.1em;
  color: transparent;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 0 -30px;
}
.starability-growRotate > label,
.starability-growRotate > label:before {
  height: 30px;
  background-image: url("data:image/png;base64,");
}
.starability-growRotate > label:before {
  content: "";
  position: absolute;
  display: block;
  background-position: 0 30px;
  pointer-events: none;
  opacity: 0;
}
.starability-growRotate > label:nth-of-type(5):before {
  width: 120px;
  left: -120px;
}
.starability-growRotate > label:nth-of-type(4):before {
  width: 90px;
  left: -90px;
}
.starability-growRotate > label:nth-of-type(3):before {
  width: 60px;
  left: -60px;
}
.starability-growRotate > label:nth-of-type(2):before {
  width: 30px;
  left: -30px;
}
.starability-growRotate > label:nth-of-type(1):before {
  width: 0;
  left: 0;
}
@media screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi) {
  .starability-growRotate > label {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
}
@media screen and (-ms-high-contrast: active) {
  .starability-growRotate {
    width: auto;
  }
  .starability-growRotate > input {
    position: static;
    margin-right: 0;
    opacity: 1;
  }
  .starability-growRotate .input-no-rate {
    display: none;
  }
  .starability-growRotate > label {
    display: inline;
    float: none;
    width: auto;
    height: auto;
    font-size: 1em;
    color: inherit;
    background: none;
  }
  .starability-growRotate > label:after,
  .starability-growRotate > label:before {
    display: none;
  }
}
.starability-growRotate > label:after {
  content: " ";
  position: absolute;
  opacity: 0;
  width: 30px;
  height: 30px;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  bottom: 0;
  left: 0;
}
@media screen and (-webkit-min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi) {
  .starability-growRotate > label:after {
    background-image: url("data:image/png;base64,");
    background-size: 30px auto;
  }
}
.starability-growRotate > input:checked + label:after {
  background-position: 0 -30px;
  opacity: 1;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-name: a;
  animation-name: a;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
